@charset "UTF-8";
* {
  margin: 0;
  padding: 0; }

a {
  text-decoration: none; }

ul,
li {
  list-style: none; }

.container {
  width: 1090px;
  margin: 0 auto; }

header .header-top {
  position: relative;
  height: 36px;
  line-height: 36px;
  background-color: #333;
  z-index: 4;
  color: #ccc; }
  header .header-top .top-left {
    float: left; }
    header .header-top .top-left > a {
      float: left; }
  header .header-top .top-right {
    float: right; }
  header .header-top a {
    color: #ccc; }
    header .header-top a:hover {
      color: #fff; }
  header .header-top .top-item {
    display: inline-block;
    margin-left: 10px;
    position: relative; }
    header .header-top .top-item::before {
      content: '';
      display: inline-block;
      height: 16px;
      border-left: 1px solid #5c5c5c;
      vertical-align: middle;
      margin-right: 10px; }
    header .header-top .top-item:first-child a:nth-child(2) {
      margin-left: 20px; }
    header .header-top .top-item:first-child::before {
      border: none; }
  header .header-top .top-notice {
    float: left;
    height: 36px;
    overflow: hidden;
    position: relative;
    padding-left: 26px;
    margin-left: 30px; }
    header .header-top .top-notice .fa {
      position: absolute;
      top: 10px;
      left: 0; }
    header .header-top .top-notice a,
    header .header-top .top-notice .fa {
      color: #b4a078; }

header .header-main {
  position: relative;
  height: 204px; }
  header .header-main .header-logo {
    text-align: center; }
  header .header-main .header-nav {
    line-height: 1;
    font-weight: 700;
    height: 30px;
    bottom: 21px;
    width: 100%; }
    header .header-main .header-nav li {
      display: inline-block;
      margin-top: 6px;
      padding: 0 24px; }
    header .header-main .header-nav .nav-dropdown {
      display: none;
      position: absolute;
      top: 21px;
      padding-top: 30px;
      background-color: transparent;
      padding: 20px 40px 30px;
      background: #fff;
      box-shadow: 0 0 1px 1px rgba(0, 0, 0, 0.1); }
    header .header-main .header-nav .nav-column {
      margin-right: 34px;
      display: inline-block; }
    header .header-main .header-nav .nav-title {
      margin-bottom: 10px;
      font-size: 16px;
      font-weight: 700;
      line-height: 36px;
      border-bottom: 1px solid #ddd; }
    header .header-main .header-nav .nav-item {
      margin-bottom: 4px;
      font-weight: 400;
      line-height: 50px; }
      header .header-main .header-nav .nav-item img {
        height: 50px;
        width: 50px;
        border: 0;
        vertical-align: top; }
      header .header-main .header-nav .nav-item span {
        display: inline-block;
        width: 84px;
        max-width: 84px;
        max-height: 48px;
        margin-left: 6px;
        font-size: 14px;
        vertical-align: middle;
        line-height: 1.2;
        overflow: hidden; }

footer .footer-top {
  padding-top: 60px;
  padding-bottom: 34px;
  background-color: #fff;
  border-top: 1px solid #e8e8e8; }
  footer .footer-top .container {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr; }
    footer .footer-top .container .top-item {
      text-align: center;
      color: #333;
      padding: 0 37px;
      height: 204px; }
      footer .footer-top .container .top-item h4 {
        font-size: 16px;
        font-weight: 400;
        margin-bottom: 28px;
        line-height: 1; }
      footer .footer-top .container .top-item a {
        display: inline-block;
        width: 80px;
        height: 100px;
        font-size: 14px;
        background-color: #fff;
        padding-top: 25px;
        text-align: center;
        border: 1px solid #E8E8E8; }
        footer .footer-top .container .top-item a i {
          font-size: 30px; }
        footer .footer-top .container .top-item a p {
          margin-top: 10px; }
        footer .footer-top .container .top-item a:hover {
          color: #b4a078; }
      footer .footer-top .container .top-item:not(:first-of-type) {
        border-left: 1px solid #e9e9e9; }

footer .footer-main {
  height: 204px;
  padding-top: 37px;
  background-color: #414141; }
  footer .footer-main .top-grid {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    margin-bottom: 30px; }
    footer .footer-main .top-grid .top-item {
      color: #fff;
      font-size: 18px;
      line-height: 51px;
      display: inline-block;
      vertical-align: middle;
      text-align: center; }
      footer .footer-main .top-grid .top-item i {
        margin-right: 17px;
        font-size: 35px;
        display: inline-block;
        width: 50px;
        height: 50px;
        border: 2px solid #fff;
        border-radius: 50%;
        padding-top: 6px; }
      footer .footer-main .top-grid .top-item span {
        vertical-align: super; }
  footer .footer-main .footer-info {
    text-align: center;
    color: #999;
    position: relative; }
    footer .footer-main .footer-info .nav-list a {
      color: #999; }

footer .footer-hr {
  height: 1px;
  margin-bottom: 27px;
  background-color: #4f4f4f;
  border: 0; }

.app-down-guide {
  width: 100%;
  height: 120px;
  position: fixed;
  bottom: 0;
  background: rgba(0, 0, 0, 0.7);
  z-index: 99; }
  .app-down-guide .content {
    width: 1090px;
    height: 90px;
    margin: 15px auto;
    position: relative; }
    .app-down-guide .content .fa-close {
      position: absolute;
      height: 22px;
      width: 22px;
      border-radius: 50%;
      top: -11px;
      right: -11px;
      cursor: pointer;
      background: #ffff;
      text-align: center;
      line-height: 21px; }

/** 登录页 **/
.page-box {
  background: #fff; }
  .page-box .login-box .box-tab {
    margin-top: 32px; }
    .page-box .login-box .box-tab .tab-item {
      display: none; }
      .page-box .login-box .box-tab .tab-item.active {
        display: block; }
  .page-box .register-box {
    display: none; }
  .page-box .box-main {
    margin: 0 32px; }
    .page-box .box-main .box-header {
      padding: 30px 0 10px;
      display: grid;
      grid-template-columns: 1fr 1fr;
      text-align: center; }
      .page-box .box-main .box-header a {
        color: #333; }
        .page-box .box-main .box-header a:first-child {
          border-right: 1px solid #e8e8e8; }
        .page-box .box-main .box-header a.active {
          color: #999; }
          .page-box .box-main .box-header a.active:hover {
            color: #B4A078; }
    .page-box .box-main .btn {
      width: 100%;
      margin: 40px 0 11px;
      background: #b4a078;
      height: 48px;
      line-height: 48px;
      border-radius: 2px; }
    .page-box .box-main .input-hr {
      height: 15px;
      line-height: 16px;
      font-size: 12px; }
    .page-box .box-main .input-group {
      height: 36px;
      border: 1px solid #e8e8e8;
      border-radius: 2px;
      position: relative; }
      .page-box .box-main .input-group .fa {
        position: absolute;
        display: block;
        left: 7px;
        top: 7px;
        height: 24px;
        line-height: 24px;
        width: 22px;
        font-size: 20px;
        border-right: 1px solid #e8e8e8; }
      .page-box .box-main .input-group input {
        height: 34px;
        width: 100%;
        line-height: 34px;
        padding-left: 44px;
        font-size: 14px;
        background: #fff;
        color: #333;
        border: 0;
        font-weight: normal; }
  .page-box .box-footer {
    height: 60px;
    background: #F5F3EF;
    padding-right: 25px; }
    .page-box .box-footer .login-oauth-box {
      margin: 0 0 0 32px;
      padding-top: 8px;
      height: 50px;
      font-size: 30px;
      float: left; }
      .page-box .box-footer .login-oauth-box a {
        margin: 0 4px;
        color: #999; }
        .page-box .box-footer .login-oauth-box a:hover {
          color: #B4A078; }

.login-page {
  width: 100%;
  height: 600px;
  background: center no-repeat; }
  .login-page .container {
    position: relative; }
    .login-page .container .page-box {
      float: right;
      margin-top: 83px;
      width: 386px; }
